@import "vars";

.secSitemap01 {
    padding: 0 15px;

    &aa {
        margin-top: 45px;
        margin-bottom: 80px;

        @include mq(md) {
            margin-top: 62px;
            margin-bottom: 0;
        }
    }

    &ab {
        margin-bottom: 80px;

        @include mq(md) {
            margin-bottom: 0 !important;

        }
    }

    &ac {
        margin-bottom: 96px;

        @include mq(md) {
            margin-bottom: 100px !important;

        }
    }

    >.container {
        >.grid {
            color: red;

            display: grid;
            grid-template-columns: repeat(4, 1fr);

            @include mq(md) {
                grid-template-columns: repeat(1, 1fr);
            }

            >.col {
                @include mq(md) {
                    margin-bottom: 40px;

                }

                >.nav {
                    >.item {
                        font-size: 17px;
                        font-weight: 500;
                        line-height: 50px;
                        color: #080808;
                        display: block;
                        height: 29px;
                        display: flex;
                        align-items: center;
                        padding-left: 25px;
                        &:not(:last-child) {
                            margin-bottom: 21px;

                        }
                        >svg {
                            margin-right: 15px;

                        }
                    }

                    >.item01 {
                        font-size: 20px !important;
                        font-weight: bold !important;
                        padding-left: 0 !important;
                    }
                    >.item02 {
                        padding-left: 50px !important;
                    }
                    >.item03 {
                        @include mq(md) {
                            display: none !important;
                        }
                    }
                }

            }

            >.col04 {
                @include mq(md) {
                    display: none !important;
                }
            }
        }
    }
}